body {
  justify-content: center;
  align-items: center;
  height: 100vh;
  margin: 0;
  display: flex;
}

.stopwatch {
  border: 1vmin dotted #000;
  border-radius: 50%;
  grid-template-rows: repeat(2, 1fr);
  justify-content: center;
  place-items: flex-end center;
  width: 80vmin;
  height: 80vmin;
  display: grid;
}

.stopwatch__minutes-hand {
  transform-origin: bottom;
  background-color: #0700ff;
  grid-area: 1 / 2;
  width: 3vmin;
  height: 20vmin;
  animation: 3600s steps(60, end) infinite hand-move;
}

.stopwatch__seconds-hand {
  transform-origin: bottom;
  background-color: #2c8000;
  grid-area: 1 / 2;
  width: 1.5vmin;
  height: 38vmin;
  animation: 60s linear infinite hand-move;
}

.stopwatch__circle {
  z-index: 1;
  background-color: #f6a603;
  border-radius: 50%;
  grid-area: 1 / 2;
  width: 5vmin;
  height: 5vmin;
  margin-bottom: -2.5vmin;
}

.stopwatch--speed-up .stopwatch__minutes-hand {
  animation-duration: 600s;
}

.stopwatch--speed-up .stopwatch__seconds-hand {
  animation-duration: 10s;
}

@keyframes hand-move {
  from {
    transform: rotate(0);
  }

  to {
    transform: rotate(360deg);
  }
}

